* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

#root {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  padding: 20px;
  font-size: 18px;
  font-family: sans-serif;
  background-color: #e7ecf2;
}

